	export default {
		data() {
			return {
				dotsStyles: {
					backgroundColor: 'rgba(249, 249, 251, 1)',
					border: 'none',
					color: '#fff',
					selectedBackgroundColor: 'rgba(3, 190, 144, 1)',
					selectedBorder: 'none'
				},
				mode: 'dot',
				current:0,
				tabBars:[],
				bannerList: [],
				hotProductList:[],
				newProductList:[],
                goodsNum:0
			};
		},
        onShow() {
            var cartList = uni.getStorageSync('community_cartList')
            this.goodsNum = 0
			if(cartList){
				// cartList.forEach(item=>{
				// 	this.goodsNum += item.number
				// })	
                this.goodsNum = cartList.length
			}
        },
		onLoad() {
			console.log(this.communityId);
			this.requestClassifyData();
			this.getHotProduct();
			this.getBannerList();
            
		},
		computed: {
			communityId(){
				return this.$store.getters.communityId
			}
		},
		methods: {
            clickGwc:function(){
                uni.navigateTo({
                	url:`/pages/cart/cart?type=community`
                })
            },
			search(res) {
				uni.navigateTo({
					url: `/pages/health-search-page/health-search-page?value=${res.value}&type=community`,
				});
			},
			change(e) {
				this.current = e.detail.current
			},
			ontabtap(tab) {
				console.log(tab);
			    uni.navigateTo({
			    	url: `/pages/health-mall-community/health-mall-community2?title=${tab.label}&classifyId=${tab.id}`,
			    });
			},
			goDetail(id,type){
				uni.navigateTo({
					url: `/pages/health-product-detail/health-product-detail?id=${id}&type=${type}`,
				});
			},
			requestClassifyData(){
				this.$api.productClassifyList().then(res=>{
					if(res.status=="OK"){
						let arr = res.data
						arr.map(item=>{
							this.tabBars.push({
								id:item.id,
								label:item.name,
								value:''
							})
						})
					}
				}).catch(err=>{
					console.log(err);
				})
			},
			getHotProduct(){
				this.$api.communityProductList({
					size:999,
					page:1,
					hot:true,
				}).then(res=>{
					if(res.status=="OK"){
						res.list.map(item=>{
							this.hotProductList.push({
								price:item.price/100,
								originalPrice:item.originalPrice/100,
								name:item.name,
								pic:JSON.parse(item.icon)[0].url,
								id:item.id
							})
						})
					}
				}).catch(err=>{
					console.log(err);
				})
			},
			getBannerList(){
				this.$api.communityProductList({
					size:999,
					page:1,
					banner:true,
				}).then(res=>{
					if(res.status=="OK"){
						res.list.map(item=>{
							this.bannerList.push({
								url:JSON.parse(item.bannerPic)[0].url,
								name:item.name,
								id:item.id
							})
						})
					}
				}).catch(err=>{
					console.log(err);
				})
			}
		},
		
	}
